.checkbox-custom {
	font-size: 12px;
	line-height: 20px;

	&:before {
		border: none;
		border-radius: 0;
		content: " ";
		padding: 0;
		.sprite(-2px, -1px);
	}

	&:hover {
		&:before {
			.sprite(-17px, -1px);
		}

		&.checked {
			&:before {
				.sprite(-34px, -1px);
			}
		}
	}

	&.checked {
		&:before {
			content: " ";
			.sprite(-49px, -1px);
		}
	}

	&.disabled {
		&:before {
			opacity: 1;
			.sprite(-65px, -1px);
		}

		&.checked {
			&:before {
				.sprite(-81px, -1px);
			}
		}
	}
	
	&.checkbox-inline {
		input {
			position: absolute;
		}
	}
}

@media(-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
	.checkbox-custom:before,
	.checkbox-custom:hover:before,
	.checkbox-custom.checked:before,
	.checkbox-custom.checked:hover:before,
	.checkbox-custom.disabled:before,
	.checkbox-custom.disabled.checked:before
	{
		.sprite2x();
	}
}
