//panel with tables
@import "../fuelux-override/variables";
@import "../fuelux-override/mixins";

.panel {
	.table {
		border: 0;
	}
}

.panel-body {
	padding: 15px 10px;
}

.panel-heading {
	background-color: #f0f2f3;
	padding: 10px;
	border-bottom: 1px solid transparent;
	.box-shadow(inset 0 0 2px #fff);

	.panel-title {
		font-size: 14px;
		line-height: 21px;	// to center align with right-floated buttons
	}
	
}

.panel-footer {
	.footer-with-inputs();
	.box-shadow(inset 0 0 2px #fff);
}

// Contextual variations
.panel-default {
	.panel-variant(@panel-default-border; @panel-default-text; @panel-default-heading-bg; @panel-default-border);
}
.panel-primary {
	.panel-variant(@panel-primary-border; @panel-primary-text; @panel-primary-heading-bg; @panel-primary-border);
}
.panel-success {
	.panel-variant(@panel-success-border; @panel-success-text; @panel-success-heading-bg; @panel-success-border);
}
.panel-info {
	.panel-variant(@panel-info-border; @panel-info-text; @panel-info-heading-bg; @panel-info-border);
}
.panel-warning {
	.panel-variant(@panel-warning-border; @panel-warning-text; @panel-warning-heading-bg; @panel-warning-border);
}
.panel-danger {
	.panel-variant(@panel-danger-border; @panel-danger-text; @panel-danger-heading-bg; @panel-danger-border);
}

// Combining panels and a dropdown arrow option
// -------------------------

.collapse-dropdown {
	border: 0;

	.panel {
		border: none;

		.panel-heading {
			background-color: transparent;
			border: none;
			padding: 5px 10px;
			.glyphicon-play {
				font-size: 10px;
				padding-right: 5px;
				padding-left: 7px;
				top: -1px;
				left: 0;
				border: 0;
				margin-right: 5px;
				background: transparent url(../img/spritesheet.png) no-repeat -176px -16px;
				width: 13px;
				height: 13px;
				&:before {
					display: none;
				}
			}
			a.collapsed {
				.glyphicon-play {
					background: transparent url(../img/spritesheet.png) no-repeat -176px 0;
				}
			}
		}
	}
	&.panel-group .panel-heading + .panel-collapse > .panel-body {
		border: none;
		padding: 0 30px 10px;
	}

